import React from 'react';
import './App.scss';

class App extends React.Component {
  state = {
    count: 0,
  }

  addOne = () => {
    this.setState(state => ({
      count: state.count + 1,
    }));
  };

  add100 = (i) => {
    this.setState(state => ({
      count: (state.count + 100) + i,
    }));
  };

  increase = () => {
    (this.state.count + 1) % 5 === 0
      ? this.add100(1)
      : this.addOne();
  };

  render() {
    return (
      <>
        <h1>
          Count: $
          {this.state.count}
        </h1>
        <button
          type="button"
          onClick={this.addOne}
        >
          Add One
        </button>
        <button
          type="button"
          onClick={this.add100}
        >
          Add 100
        </button>
        <button
          type="button"
          onClick={this.increase}
        >
          increase
        </button>
      </>
    );
  }
}

export default App;
